@import url(https://fonts.googleapis.com/css2?family=Biryani:wght@400;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card {
  color: #c10016;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-transform: uppercase;
  font-family: "Biryani", sans-serif;
  
}

.big-card {
  font-size: 25px;
  width: 30vw;
  height: 30vh;
  margin: 5vh;
  background-color: white;
  color: #c10016;
  border-radius: 50px;
  -webkit-filter: drop-shadow(0 0px 20px rgb(216, 216, 216));
          filter: drop-shadow(0 0px 20px rgb(216, 216, 216));
  box-shadow: 0 5px 5px 1px rgb(80, 80, 80);
  padding: 50px;
}

.small-card {
  margin: auto;
  font-size: 18px;
  width: 18vw;
  height: 20vh;
  margin-top: 1vh;
  margin-bottom: 1vh;
  border-radius: 30px;
  box-shadow: 0 5px 4px 2px rgb(187, 187, 187);
  padding: 20px;
}

.App {
  font-family: "Biryani", sans-serif;
  text-align: center;
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;
  height: 100vh;
  font-weight: 800;
}

a,
a:visited,
a:hover {
  color: #c10016;
  font-size: 12px;
  font-weight: 700;
  text-decoration: none;
}
.credits {
  font-size: 12px;
  font-weight: 400;
  background-color: white;
  height: auto;
  text-align: left;
  max-height: 13px;
  width: 175px;
  padding: 4px;
  top: 30px;
  left: 40px;
  padding-left: 10px;
  border-left: 1px solid #c10016;
  z-index: 10;
  position: fixed;
  overflow: hidden;
  transition: max-height 1.2s ease-in-out;
}

.credits:hover {
  height: auto;
  max-height: 1000px;
  transition: max-height 1.2s ease-in-out;
  -webkit-transition: max-height 1.2s ease-in-out;
  -moz-transition: max-height 1.2s ease-in-out;
  -o-transition: max-height 1.2s ease-in-out;
}

.credits-title {
  text-transform: uppercase;
  font-weight: 700;
}

.cont {
  padding-top: 10px;
  border-top: 1px solid #eee;
}

.title {
  color: #c10016;
  font-size: 30px;
  margin-top: 3vh;
  margin-bottom: 3vh;
  font-weight: 800;
}

.levels {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  width: 30vw;
}

.level-button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 15px;
  border-radius: 15px;
  border: solid 2px #c10016;
  background-color: white;
  height: 88px;
  width: 231px;
  text-transform: uppercase;
  color: #c10016;
  font-size: 18px;
  font-weight: 800;
  font-family: "Biryani", sans-serif;
}

.level-button:focus,
.level-button:active {
  outline: none;
}

.selected-level {
  background-color: #c10016;
  color: white;
  outline: none;
}

.next-card-button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 25px;
  border-radius: 15px;
  background-color: white;
  height: 88px;
  width: 231px;
  text-transform: uppercase;
  color: white;
  background-color: #c10016;
  outline: none;
  font-size: 18px;
  font-weight: 800;
  font-family: "Biryani", sans-serif;
  border: none;
}

.next-card-button:hover {
  background-color: #ad0014;
  color: rgb(240, 240, 240);
}

.question {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 40vw;
  text-transform: uppercase;
  align-items: center;
}

.history {
  display: flex;
  flex-direction: column;
  width: 30vw;
  align-content: center;
  align-items: center;
  padding: 5px;
}

.history-title {
  text-transform: uppercase;
  color: #c10016;
  font-size: 18px;
  font-weight: 800;
}

.card-container {
  height: 75vh;
  width: 25vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-content: center;
  margin: 10px;
  border-radius: 15px;
  border: solid 2px #c10016;
}

.card-container-scroll {
  width: 96%;
  height: 96%;
  overflow-y: scroll;
  overflow-x: hidden;
  margin: auto;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: white;
}

::-webkit-scrollbar-thumb {
  background: #c10016;
  border-radius: 50px;
}

